.sidebar {
z-index: 10;
}

.sidebar.open {
    position: absolute;
    width: 317px;
    height: 720px;
    left: 0px;
    top: 0px;

    background: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 0px;
}

.background {
    position: absolute;
    width: 1280px;
    height: 720px;
    left: 0px;
    top: 0px;

    background: #000000;
    opacity: 0.3;
}

